<template>
	<div class="modal modal_sc1" id="permission-add">
	    <div class="modal-dialog">
	        <div class="modal-content">
	            <!-- Modal Header -->
	            <div class="modal-header">
	                <h4 class="modal-title">Thêm mới quyền</h4>
	                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
	            </div>
	            <!-- Modal body -->
	            <div class="modal-body">
	                <div class="form-group">
	                    <label for="">Tên:</label>
	                    <input type="text" v-model="data.fullname" class="form-control">
	                </div>
                    <div class="form-group">
                        <label for="">Dự án:</label>
                        <select class="form-control">
                            <option>-- Chọn dự án --</option>
                            <option v-for="item in projects" :value="item.id">{{ item.name }}</option>
                        </select>
                    </div>
                    <div class="form-group list-module">
                        <div class="colums">
                        </div>
                    </div>
	            </div>
	            <!-- Modal footer -->
	            <div class="modal-footer">
	                <button type="button" class="btn btn-connect pull-left" @click="submit()">Xác nhận</button>
	                <button type="button" class="btn btn-danger close-modal" data-dismiss="modal">Close</button>
	            </div>
	        </div>
	    </div>
	</div>
</template>

<script>
import PermissionRepository from '@/repositories/PermissionRepository'

export default {
    data: ()=> ({
        data: {
            fullname: null, 
            email: null, 
            password: null,	
        },
        projects: []
    }),
    created() {
        this.getProject();
    },
    methods: {
        submit() {
        	UserRepository.add(this.data)
            .then(response=> {
                switch(response.status) {
                    case 1: 
                        $(this.$el).find('.close-modal').click();
                        this.$emit('reload');
                        this.$notify({
                            text: 'Tạo mới người dùng thành công',
                            type: 'success'
                        });
                    break;
                    case 0:
                        response.messages.list.map(value=> {
                            this.$notify({
                                text: value,
                                type: 'error'
                            })
                        });
                    break;
                }
            })
        },
        getProject() {
            PermissionRepository.getListProject()
            .then(response=> {
                this.projects = response;
            })
        }
    }
}
</script>